/** Reducer */
export interface Action<T = any> {
  type: T
}

export interface AnyAction extends Action {
  // Allows any extra properties to be defined in an action.
  [extraProps: string]: any
}

export type Reducer<S = any, A extends Action = AnyAction> = (
  state: S,
  action: A,
) => S

export type ReducersMapObject<S = any, A extends Action = Action> = {
  [K in keyof S]: Reducer<S[K], A>
}

export type ContextProviderType = React.FC<{ initialState: Context }>

/** Context Actions */
export enum ContextActions {
  CHANGE_THEME = 'CHANGE_THEME',
  SWITCH_THEME = 'SWITCH_THEME',
  LOADING = 'LOADING',
  NOT_LOADING = 'NOT_LOADING',
}

/** CONTEXT */
export type Context = {
  config: Config
  progressBar: AppProgressBar
}

export type UserType = 'admin' | 'staf'
export type User = {
  name: string
  type: UserType
}
export type ProgressBarVariant =
  | 'indeterminate'
  | 'determinate'
  | 'buffer'
  | 'query'
export type AppProgressBar = {
  isLoading: boolean
  color?: 'primary' | 'secondary'
  variant?: ProgressBarVariant
  value?: number
  valueBuffer?: number
  style?: any
}
export type AppProgressBarOptions = Omit<AppProgressBar, 'isLoading'>
export type ThemeName = 'light' | 'dark'
export type ThemeInfo = {
  name: ThemeName
  isDarkTheme: boolean
  isLightTheme: boolean
}
export type Config = {
  theme: ThemeInfo
}
