import React, { useEffect } from 'react'
import ThemeProvider from '@material-ui/styles/ThemeProvider'

import { ContextProvider } from '../context/'
import InitialState from '../context/initialState'
import * as Themes from './Themes'
import { getChangeBackground } from '../config'
import { useSwitchTheme } from '../effects'
import { ThemeName } from '../context/types'

const ThemeProviderWrapper = ({
  children,
  theme = 'light',
}: React.PropsWithChildren<{ theme?: ThemeName }>) => {
  const [themeInfo, switcher] = useSwitchTheme()

  useEffect(() => {
    theme !== themeInfo.name && switcher()
  }, [])

  useEffect(() => {
    if (getChangeBackground()) {
      if (themeInfo.isDarkTheme) {
        document.body.style.background = '#2b2b2b'
      } else if (themeInfo.isLightTheme) {
        document.body.style.background = '#f2f2f2'
      }
    }
  }, [themeInfo])

  return (
    <ThemeProvider theme={themeInfo.isDarkTheme ? Themes.Dark : Themes.Light}>
      {children}
    </ThemeProvider>
  )
}

export default function Provider({
  children,
  ...props
}: React.PropsWithChildren<{ theme?: ThemeName }>) {
  return (
    <ContextProvider initialState={InitialState}>
      <ThemeProviderWrapper {...props}>{children}</ThemeProviderWrapper>
    </ContextProvider>
  )
}

export const withProvider = (Component: React.FC<any>) => ({
  children,
  ...props
}: React.PropsWithChildren<any>) => (
  <Provider>
    <Component {...props}>{children}</Component>
  </Provider>
)
