import { ContextActions, ThemeName, AppProgressBarOptions } from '../types'

export const ChangeTheme = (theme: ThemeName) => ({
  type: ContextActions.CHANGE_THEME,
  theme,
})

export const SwitchTheme = () => ({
  type: ContextActions.SWITCH_THEME,
})

export const Loading = (options: AppProgressBarOptions) => ({
  type: ContextActions.LOADING,
  options,
})

export const NotLoading = () => ({
  type: ContextActions.NOT_LOADING,
})
