import _ from 'lodash'

import { Reducer, ContextActions, AppProgressBar } from './types'

export const progressBar: Reducer<AppProgressBar> = (state, action) => {
  switch (action.type) {
    case ContextActions.LOADING:
      return _.assign(state, {
        isLoading: true,
        ...action.options,
      })
    case ContextActions.NOT_LOADING:
      return {
        isLoading: false,
      }
    default:
      return state
  }
}
