import React from 'react'
import clsx from 'clsx'

import { ProfileAvatarProps } from '../types'
import { Avatar, makeStyles, Theme, IconButton } from '../../core'

const useStyles = makeStyles((theme: Theme) => ({
  avatar: {
    width: 32,
    height: 32,
  },
  letter: {
    padding: 2,
  },
}))

export const ProfileAvatar = ({
  src,
  alt,
  Slot,
  onClick,
}: ProfileAvatarProps & {
  onClick: (anchor: HTMLElement, Slot: React.FC) => void
}) => {
  const classes = useStyles()
  const AvatarElement = () =>
    src ? (
      <Avatar className={classes.avatar} src={src} />
    ) : (
      <Avatar className={clsx(classes.avatar, classes.letter)}>{alt}</Avatar>
    )
  return (
    <>
      {Slot ? (
        <IconButton
          size='small'
          onClick={(e: any) => onClick(e.currentTarget, Slot)}
        >
          <AvatarElement />
        </IconButton>
      ) : (
        <AvatarElement />
      )}
    </>
  )
}
