import React from 'react'
import clsx from 'clsx'

import { ThemeSwitcher } from '../ThemeSwitcher'
import { Menu as MenuIcon, ChevronLeft as ChevronLeftIcon } from '../../icons'
import {
  AppBar,
  IconButton,
  Drawer,
  makeStyles,
  Theme,
  Divider,
  Box,
  Typography,
  Toolbar,
  LinearProgress,
  Popper,
  Fade,
  Paper,
} from '../../core'
import { useMouseHover, ProfileAvatar } from '../..'
import { useLoadingStatus } from '../../effects'
import { AppBarProps } from '../types'

const drawerWidth = 240

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: theme.spacing(7),
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}))

export function Layout({
  children,
  AfterTitleSlot,
  MenuSlot,
  title,
  showProfile = false,
  hideThemeSwitch = false,
}: AppBarProps) {
  const classes = useStyles()

  // LEFT MENU
  const [menuPinned, setPinMenu] = React.useState(false)
  const [mouseHoverLeftMenu, mouseHoverLeftMenuHandler] = useMouseHover()
  const openLeftMenu = menuPinned || mouseHoverLeftMenu
  const [{ isLoading, ...progressBarProps }] = useLoadingStatus()
  const handleDrawerOpen = () => {
    setPinMenu(true)
  }
  const handleDrawerClose = () => {
    setPinMenu(false)
    mouseHoverLeftMenuHandler.onMouseLeave()
  }

  // POPPER
  const [popperAnchorEl, setPopperAnchorEl] = React.useState<null | {
    target: HTMLElement
    Slot: React.FC
  }>(null)
  const openPopper = (anchorEl: HTMLElement, Slot: React.FC) => {
    setPopperAnchorEl({ target: anchorEl, Slot })
  }
  return (
    <div className={classes.root}>
      <AppBar
        position='fixed'
        color='default'
        className={clsx(classes.appBar, {
          [classes.appBarShift]: openLeftMenu,
        })}
      >
        <Toolbar variant='dense'>
          {MenuSlot && !openLeftMenu && (
            <IconButton
              edge='start'
              color='inherit'
              aria-label='Menu'
              onClick={handleDrawerOpen}
            >
              <MenuIcon />
            </IconButton>
          )}
          <Box ml={1}>
            <Typography variant='h6' color='inherit' noWrap>
              {title}
            </Typography>
          </Box>
          {AfterTitleSlot && <AfterTitleSlot />}
          {!hideThemeSwitch && (
            <Box component='span' ml='auto' mr={1}>
              <ThemeSwitcher />
            </Box>
          )}
          {showProfile && (
            <ProfileAvatar {...showProfile} onClick={openPopper} />
          )}
        </Toolbar>
        {isLoading && <LinearProgress {...progressBarProps} />}
      </AppBar>
      {MenuSlot && (
        <Drawer
          variant='permanent'
          open={openLeftMenu}
          {...mouseHoverLeftMenuHandler}
          className={clsx(classes.drawer, {
            [classes.drawerOpen]: openLeftMenu,
            [classes.drawerClose]: !openLeftMenu,
          })}
          classes={{
            paper: clsx({
              [classes.drawerOpen]: openLeftMenu,
              [classes.drawerClose]: !openLeftMenu,
            }),
          }}
        >
          <div className={classes.toolbar}>
            <IconButton onClick={handleDrawerClose}>
              <ChevronLeftIcon />
            </IconButton>
          </div>
          <Divider />
          <MenuSlot open={openLeftMenu} />
        </Drawer>
      )}
      <Popper
        placement='bottom-end'
        anchorEl={popperAnchorEl && popperAnchorEl.target}
        open={popperAnchorEl ? true : false}
        transition
      >
        {({ TransitionProps }) => (
          <Fade {...TransitionProps} timeout={350}>
            <Paper onMouseLeave={() => setPopperAnchorEl(null)}>
              {popperAnchorEl && React.createElement(popperAnchorEl.Slot)}
            </Paper>
          </Fade>
        )}
      </Popper>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        {children}
      </main>
    </div>
  )
}
