import { useContext } from '../context/'
import { Loading, NotLoading } from '../context/constraints'
import { AppProgressBar } from '../context/types'

export const useLoadingStatus = () => {
  const [
    {
      progressBar: { isLoading, ...pbp },
    },
    dispatcher,
  ] = useContext()
  const setLoading = ({ isLoading, ...options }: AppProgressBar) => {
    dispatcher(isLoading ? Loading(options) : NotLoading())
  }
  return [{ isLoading, ...pbp }, setLoading] as [
    AppProgressBar,
    (options: AppProgressBar) => void
  ]
}
