import React from 'react'

import {
  Typography,
  makeStyles,
  createStyles,
  Card,
  CardContent,
  Box,
  Grid,
} from '../lib/core'
import { PageProps, useMouseHover, useLoadingStatus } from '../lib'

const useStyles = makeStyles(
  createStyles({
    card: {
      boxShadow: '0',
    },
    cardRaised: {
      boxShadow: '3',
    },
  }),
)

const LoadingItem = () => {
  const [{ isLoading }, setLoading] = useLoadingStatus()
  return (
    <CardItem
      title={isLoading ? 'Stop Loading' : 'Start Loading'}
      content='Show App Progress Bar'
      onClick={() => setLoading({ isLoading: !isLoading })}
    />
  )
}

const CardItem = ({
  title,
  content,
  onClick,
}: {
  title: string
  content: string
  onClick: () => void
}) => {
  const classes = useStyles({})
  const [mouseHover, mouseHoverHandler] = useMouseHover()

  return (
    <Grid item xs={3}>
      <Card
        className={classes.card}
        raised={mouseHover}
        {...mouseHoverHandler}
        onClick={onClick}
      >
        <CardContent>
          <Typography variant='h6'>{title}</Typography>
          <Typography variant='body2' component='p'>
            {content}
          </Typography>
        </CardContent>
      </Card>
    </Grid>
  )
}
const Topic = ({
  title,
  children,
}: React.PropsWithChildren<{ title: string }>) => {
  return (
    <>
      <Box paddingY={4}>
        <Typography variant='h4' color='textSecondary'>
          {title}
        </Typography>
      </Box>
      <Box>{children}</Box>
    </>
  )
}
export default ({ navigate }: PageProps) => {
  return (
    <>
      <Box paddingX={5} marginTop={3}>
        <Topic title='Form Controllers'>
          <Grid container spacing={2}>
            <CardItem
              title='TextField'
              content='Text input, textarea, datepicker, number'
              onClick={() => (navigate as any)('textfield')}
            />
            <CardItem
              title='Checks'
              content='Checkbox, CheckboxList'
              onClick={() => (navigate as any)('checks')}
            />
            <CardItem
              title='Switches'
              content='Switch, SwitchList'
              onClick={() => (navigate as any)('switches')}
            />
            <CardItem
              title='RadioButton'
              content='Just a RadioButton'
              onClick={() => (navigate as any)('radio')}
            />
            <CardItem
              title='Selections'
              content='Select, Autocomplete, Multi Select'
              onClick={() => (navigate as any)('selections')}
            />
          </Grid>
        </Topic>
        <Topic title='Features'>
          <LoadingItem />
        </Topic>
      </Box>
    </>
  )
}
